@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;700&family=Potta+One&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}
* {
  box-sizing: border-box;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

body {
  font-family: 'Nunito', sans-serif;
    padding: 0;
    margin: 0;
    text-align: center;
  }
h1, h2, h3, h5, h6 {
  font-weight: 300;
  font-family: 'Potta One', cursive;
}
h4 {
  font-weight: normal;
  color: slategray;
}
.logo {
  position: fixed;
  right: 50px;
  margin: 5px;
  width: 40px;
  height: 40px;
  cursor: pointer;
  z-index: 10000;
  transition: ease-in-out 1s;
  border-radius: 50%;
  background: linear-gradient(to bottom right, #04960b, #06460a);
  object-fit: contain;
  padding: 6px;
  box-shadow: rgba(0, 0, 0, 0.35) 2px 2px 4px;
}
@media only screen and (max-width: 768px) {
  .logo {
    right: 10px;
  }
}
.landing{max-width:100vw;overflow-x:hidden;height:auto}.landing .hero{position:relative;min-width:100vw;min-height:100vh}.landing .hero .bg{position:absolute;width:100vw}.landing .hero .clouds{background-image:url(/static/media/clouds.965074e1.svg);width:50000px;background-position:top;background-repeat:repeat-x;background-size:contain;height:40vh;top:0;animation:myAnim linear 2000s infinite}@keyframes myAnim{from{left:0}to{left:-50000px}}.landing .hero .grass{background-image:url(/static/media/grass.a13b059b.svg);background-repeat:no-repeat;background-size:cover;height:20vh;bottom:0}.landing .hero .title{padding-top:6vh;text-align:center;will-change:transform;cursor:pointer;text-shadow:rgba(0,0,0,0.35) 0px 6px 10px}.landing .hero .title h1{font-weight:normal;margin-bottom:-16px}.landing .hero .title h4{color:slategray}.landing .hero .avatoon{width:250px;height:250px;position:absolute;bottom:10vh;left:50%;transform:translate(-50%);background-repeat:no-repeat;background-position:center;background-size:contain;transition:all 1s ease-in-out}.landing .content{margin-top:-10px;width:100%;color:whitesmoke;z-index:3;background-color:#04960b;text-align:center}.landing .content .intro{margin:auto;max-width:768px;padding:100px 10px 200px}.landing .content .intro h2{font-weight:300}.landing .content .intro a{color:white}

.skills{display:flex;flex-wrap:wrap;max-width:1040px;margin:-100px auto 100px;background-color:white;border:1px solid #cce4d8;border-radius:20px;overflow:hidden}.skills .skill{flex:1 1;padding:25px 25px 0;text-align:center}.skills .skill img{margin:25px auto}.skills .skill .skill-title{margin-bottom:50px}.skills .skill .skill-title h5{font-family:'Nunito', cursive;font-weight:bolder;font-size:20px;color:#04960b;margin-bottom:14px}.skills .skill .skill-title p{font-size:16px;color:slategray;margin-top:-8px}.skills .frontend{border-left:1px solid #cce4d8;border-right:1px solid #cce4d8}@media only screen and (max-width: 768px){.skills{margin:-100px 10px 50px}.skills .skill{min-width:calc(100% - 40px)}.skills .skill h2:hover,.skills .skill h5:hover{text-shadow:rgba(0,0,0,0.35) 0px 2px 6px}.skills .frontend{border-right:0px;border-left:0px;border-top:1px solid #cce4d8;border-bottom:1px solid #cce4d8}}

.portfolio{max-width:1040px;margin:auto auto 100px}.portfolio h4{margin:-12px auto 60px}.portfolio .projects{display:flex;justify-content:space-between;flex-wrap:wrap}@media only screen and (max-width: 768px){.portfolio .projects{justify-content:space-evenly}}.portfolio .projects .project{min-width:270px;height:165px;grid-gap:5%;gap:5%;margin-bottom:10%;position:relative;transition:all 1s ease-in-out;cursor:pointer}.portfolio .projects .project h3{display:none}.portfolio .projects .project:hover{transform:scale(1.4);z-index:200}@media only screen and (max-width: 768px){.portfolio .projects .project:hover{transform:scale(1.2)}}.portfolio .projects .project:hover .websites,.portfolio .projects .project:hover .apps{-webkit-filter:brightness(50%);filter:brightness(50%)}.portfolio .projects .project:hover h3{display:block}.portfolio .projects .project .websites{position:absolute;left:6px;top:6px;width:238px;height:134px;background-size:cover;background-position:top;background-repeat:no-repeat;transition:all 1s ease-in-out}.portfolio .projects .project .apps{position:absolute;left:221px;top:74px;width:48px;height:84px;background-size:cover;background-position:top;background-repeat:no-repeat;border-radius:2px;transition:all 1s ease-in-out}.portfolio .projects .project .desktop{background:url(/static/media/Desktop.a96c9b69.svg);position:absolute;background-size:contain;background-position:top;background-repeat:no-repeat;width:250px;height:100%;top:0;left:0}.portfolio .projects .project .mobile{top:73px;left:220px;position:absolute;background:url(/static/media/Mobile.7f70c7ab.svg);background-size:contain;background-position:top;background-repeat:no-repeat;width:50px;height:95px}.portfolio .projects .project h3{position:absolute;color:white;padding:15px;z-index:250;max-width:230px;top:0;left:0;pointer-events:none}

.footer{background-color:#04960b;position:relative;color:white}.footer .dark{background-color:#06460a;max-width:1040px;border-radius:20px;margin:auto;position:relative;top:-50px;display:flex;justify-content:space-between;align-items:center;padding:20px 70px;text-align:center;box-shadow:rgba(0,0,0,0.35) 0px 6px 10px}@media only screen and (max-width: 768px){.footer .dark{margin:auto 10px;flex-wrap:wrap;padding:40px 15px;justify-content:center;grid-gap:20px;gap:20px}}.footer .dark h2{margin:0}.footer .dark p{max-width:350px;font-size:16px}.footer .dark p big{font-size:24px}.footer .dark button{width:200px;background-color:transparent;border:2px solid #04960b;border-radius:25px;font-size:20px;height:50px;color:white;cursor:pointer;transition:.4s linear}.footer .dark button:hover{background-color:#04960b;border:2px solid #04960b}.footer .dark button:focus{outline:none;border:2px solid #04960b}.footer .white-logo{height:80px;margin:auto;object-fit:contain}.footer p{font-size:20px}.footer .social{display:flex;justify-content:center;grid-gap:25px;gap:25px;padding:50px 0}.footer .social .social-link{cursor:pointer;width:50px;height:50px;border-radius:50%;display:flex;justify-content:center;align-items:center;box-shadow:10px 10px 20px #037d09, -10px -10px 20px #05b00d}.footer .social .social-link:active,.footer .social .social-link:hover{box-shadow:inset 10px 10px 20px #037d09, inset -10px -10px 20px #05b00d}.footer .social .social-link:active svg,.footer .social .social-link:hover svg{width:23px;height:23px}.footer .social .social-link svg{width:20px;height:20px;transition:all .1s ease-in-out}.footer h5{margin:0;padding:20px 0}.footer h5 b{font-size:20px}.footer h5 b:first-child{color:#8859fe}

