@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;700&family=Potta+One&display=swap');

body {
  font-family: 'Nunito', sans-serif;
    padding: 0;
    margin: 0;
    text-align: center;
  }
h1, h2, h3, h5, h6 {
  font-weight: 300;
  font-family: 'Potta One', cursive;
}
h4 {
  font-weight: normal;
  color: slategray;
}
.logo {
  position: fixed;
  right: 50px;
  margin: 5px;
  width: 40px;
  height: 40px;
  cursor: pointer;
  z-index: 10000;
  transition: ease-in-out 1s;
  border-radius: 50%;
  background: linear-gradient(to bottom right, #04960b, #06460a);
  object-fit: contain;
  padding: 6px;
  box-shadow: rgba(0, 0, 0, 0.35) 2px 2px 4px;
}
@media only screen and (max-width: 768px) {
  .logo {
    right: 10px;
  }
}